<template>
  <div class="broadcast">
    <img @click="goHome()" src="../assets/images/icon_return@2x.png" alt />
    <van-tabs
      v-model="activeName"
      swipe-threshold="6"
      duration="0.6"
      sticky
      swipeable
      animated
      line-width="60"
    >
      <van-tab
        :title="item.broadcasttype"
        :name="item.broadcasttype"
        v-for="(item,index) of broadcastArr"
        :key="index"
      >
        <div class="content" v-for="(item,index) of item.list" :key="index">
          <div class="con_top">
            <p></p>
            <p>{{item.broadcastname}}</p>
          </div>
          <div class="con_bottom">
            <span
              v-show="new Date(item.starttime).toLocaleDateString()===new Date().toLocaleDateString()?!item.broadcastnamedef:item.broadcastnamedef"
            >今天</span>
            <p
              v-show="new Date(item.starttime).toLocaleDateString()===new Date().toLocaleDateString()?item.broadcastnamedef:!item.broadcastnamedef"
            >{{new Date(item.starttime).getFullYear().toString().padStart(2,0)}}/{{(new Date(item.starttime).getMonth()+1).toString().padStart(2,0)}}/{{new Date(item.starttime).getDate().toString().padStart(2,0)}}</p>
            <p>{{new Date(item.starttime).getHours().toString().padStart(2,0)}}:{{new Date(item.starttime).getMinutes().toString().padStart(2,0)}}</p>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
export default {
  data() {
    return {
      activeName: this.$route.query.name,
      broadcastArr: [],
      userInfoObj: this.$store.state.userInfoObj || {}
    };
  },
  created() {
    this.$nextTick(() => {
      this.$axios
        .post("/hopeBroadcast/queryHopeBroadcastDetail", {
          // aamid: "010203",
          // deptid: "0010101032",
          // odeptid: "0010100003"
          "aamid": this.userInfoObj.aamid,
          "deptid": this.userInfoObj.deptid,
          "odeptid": this.userInfoObj.odeptid
        })
        .then(res => {
          if (res.status === 200 && res.data.status === "000000") {
            this.broadcastArr = [...res.data.data] || [];
          }
        });
    });
  },
  methods: {
    goHome() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
.broadcast {
  background: #ffffff;
  min-height: 100%;
  overflow: hidden;
  img {
    width: 0.2rem;
    height: 0.2rem;
    position: fixed;
    left: 0.03rem;
    top: 0.13rem;
    z-index: 200;
  }
  /deep/ .van-tabs__wrap {
    background: #fff;
    border-bottom: 1px solid #eaeaea;
    padding-left: 0.22rem;
    box-sizing: border-box;
    z-index: 100;
    .van-tabs__line {
      background-color: #45a4b1;
    }
    .van-tab--active {
      font-weight: bold;
    }
  }
  .content {
    width: 100%;
    border-bottom: 1px solid #eaeaea;
    padding: 0 0.1rem;
    box-sizing: border-box;
    .con_top {
      margin-top: 0.16rem;
      width: 100%;
      display: flex;
      justify-content: space-between;
      p:first-of-type {
        width: 0.06rem;
        height: 0.06rem;
        background: #45a4b1;
        border-radius: 50%;
        margin-top: 0.08rem;
      }
      p:last-of-type {
        width: 3.4rem;
        font-size: 0.16rem;
        color: #3a3a3a;
        line-height: 0.22rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .con_bottom {
      width: 100%;
      height: 0.16rem;
      padding-left: 2.6rem;
      box-sizing: border-box;
      margin-bottom: 0.095rem;
      font-size: 0.11rem;
      color: #969696;
      display: flex;
      justify-content: space-between;
      span {
        width: 0.55rem;
        text-align: center;
      }
    }
  }
}
</style>